import React from "react";
import Button from "../../layout/button/button.component";
import arrowLeft from "../../../resources/icons/arrow-left.svg";
import arrowRight from "../../../resources/icons/arrow-right.svg";
import { Wrapper, MainImg, LeftArrow, RightArrow } from "./slider.styles";

export type ModalImgProps = {
  images: string[];
};

const ModalImg = ({ images }: ModalImgProps) => {
  const [imgs, setImgs] = React.useState<string[]>(images);
  const [count, setCount] = React.useState<number>(0);

  const countPlus = () => {
    if (count === images.length - 1) {
      setCount(0);
      return;
    }
    setCount(count + 1);
  };

  const countMinus = () => {
    if (count === 0) {
      setCount(images.length - 1);
      return;
    }
    setCount(count - 1);
  };

  return (
    <Wrapper>
      <LeftArrow>
        <Button circle reverse onClick={countMinus}>
          <img src={arrowLeft} alt="" />
        </Button>
      </LeftArrow>
      <MainImg
        key={count}
        src={imgs[count]}
        onError={() => setImgs(["/img/notfound.png"])}
      />
      <RightArrow>
        <Button circle reverse onClick={countPlus}>
          <img src={arrowRight} alt="" />
        </Button>
      </RightArrow>
    </Wrapper>
  );
};

export default ModalImg;
