import { observable, action, runInAction } from "mobx";
import { mailer } from "../api";

import RootStore from "./root.store";

class MailerStore {
  rootStore: RootStore;

  constructor(rootStore: RootStore) {
    this.rootStore = rootStore;
  }

  @observable isMailSended: boolean = false;
  @observable errorMessage: string | null = null;
  @observable name: string = "";
  @observable phone: string = "";
  @observable email: string = "";
  @observable message: string = "";
  @observable prefer: string = "phone";
  @observable date: string = "";
  @observable address: string = "";

  @action setValue = (name: string, value: string) => {
    switch (name) {
      case "name":
        this.name = value;
        break;
      case "email":
        this.email = value;
        break;
      case "phone":
        this.phone = value;
        break;
      case "message":
        this.message = value;
        break;
      case "prefer":
        this.prefer = value;
        break;
      case "date":
        this.date = value;
        break;
      case "address":
        this.address = value;
        break;
    }
  };

  @action clearForm = () => {
    this.name = "";
    this.phone = "";
    this.email = "";
    this.message = "";
  };

  @action sendMessage = async () => {
    this.isMailSended = true;

    try {
      await mailer.sendMessage({
        name: this.name,
        phone: this.phone,
        email: this.email,
        message: this.message,
        prefer: this.prefer,
        date: this.date,
        address: this.address,
      });
      runInAction("Send message", () => {
        this.isMailSended = false;
        this.message = "";
      });
    } catch (err) {
      runInAction("Send message", () => {
        this.errorMessage = err.message;
        this.isMailSended = false;
      });
    }
  };

  @action sendCart = async () => {
    this.isMailSended = true;
    try {
      await mailer.sendCart(
        {
          name: this.name,
          phone: this.phone,
          email: this.email,
          message: this.message,
          prefer: this.prefer,
          date: this.date,
          address: this.address,
        },
        {
          cartList: this.rootStore.cartStore.cartList,
          total: 0,
          qty: 0,
        }
      );

      this.rootStore.cartStore.clearCart();
    } catch (err) {
      runInAction("Send cart", () => {
        this.errorMessage = err.message;
        this.isMailSended = false;
      });
    }
  };
}

export default MailerStore;
