import { observable, action, runInAction } from "mobx";

import { api } from "../api";

import RootStore from "./root.store";

class CategoryStore {
  rootStore: RootStore;

  constructor(rootStore: RootStore) {
    this.rootStore = rootStore;
  }

  @observable categoryVisible: boolean = false;
  @observable categoriesMap: Map<string, string[]> = new Map();

  @action setCategoryVisible = (visible: boolean) => {
    this.categoryVisible = visible;
  };

  @action fetchCategories = async () => {
    try {
      const categories = await api.fetchCategories();
      if (!categories) return;

      categories.sort((a: any, b: any) => {
        if (a.name > b.name) {
          return 1;
        }
        if (a.name < b.name) {
          return -1;
        }
        return 0;
      });

      categories.forEach((category) => {
        let arr = this.categoriesMap.get(category.name[0]);

        runInAction("fetch categories", () => {
          if (!arr) arr = [];
          arr.push(category.name);
          this.categoriesMap.set(category.name[0], arr);
        });
      });
    } catch (err) {
      console.error(err.message);
    }
  };
}

export default CategoryStore;
