import React from "react";
import { observer } from "mobx-react-lite";

import Button from "../../layout/button/button.component";
import cartIcon from "../../../resources/icons/cart.svg";
import categoriesIcon from "../../../resources/icons/categories.svg";
import searchIcon from "../../../resources/icons/search2.svg";
import useStore from "../../../hooks/use-store.hook";

import { Wrapper, Icon, ButtonWrapper } from "./navbar.styles";

const Navbar = () => {
  const { cartStore, categoryStore } = useStore();

  const searchHandler = () => {
    categoryStore.setCategoryVisible(false);
    cartStore.setCartVisible(false);
  };

  const categoriesHandler = () => {
    categoryStore.setCategoryVisible(true);
    cartStore.setCartVisible(false);
  };
  const cartHandler = () => {
    categoryStore.setCategoryVisible(false);
    cartStore.setCartVisible(true);
  };

  return (
    <Wrapper>
      <ButtonWrapper>
        <Button
          circle={true}
          reverse={!categoryStore.categoryVisible && !cartStore.cartVisible}
          onClick={searchHandler}
        >
          <Icon src={searchIcon} />
        </Button>
      </ButtonWrapper>
      <ButtonWrapper>
        <Button
          circle={true}
          reverse={categoryStore.categoryVisible}
          onClick={categoriesHandler}
        >
          <Icon src={categoriesIcon} />
        </Button>
      </ButtonWrapper>
      <ButtonWrapper>
        <Button
          circle={true}
          reverse={cartStore.cartVisible}
          onClick={cartHandler}
        >
          <Icon src={cartIcon} />
        </Button>
      </ButtonWrapper>
    </Wrapper>
  );
};

export default observer(Navbar);
