import React from "react";
//import _ from "lodash";
import { observer } from "mobx-react-lite";
import useStore from "../../../hooks/use-store.hook";

import searchIcon from "../../../resources/icons/search.svg";

import {
  Wrapper,
  InputWrapper,
  StyledInput,
  Logo,
  Icon,
  ExampleText,
  StyledUl,
  StyledLi,
  StyledHidenLi,
} from "./search.styles";

const Search = () => {
  const store = useStore();

  // const [debouncedHandleChange] = React.useState(() =>
  //   _.debounce(() => {
  //     store.productStore.fetchProducts(false);
  //   }, 1000)
  // );

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    store.filterStore.setSearchQuery(e.currentTarget.value);
    store.productStore.fetchProducts(false);
    //debouncedHandleChange();
  };

  const handleChangeExample = (example: string) => {
    store.filterStore.setSearchQuery(example);
    store.productStore.fetchProducts(false);
    //debouncedHandleChange();
  };

  return (
    <Wrapper>
      <InputWrapper>
        <Icon src={searchIcon} />
        <StyledInput
          value={store.filterStore.searchQuery}
          onChange={handleChange}
          placeholder="Что вы хотите найти?"
        />
        <Logo>Magmer</Logo>
      </InputWrapper>
      <ExampleText>
        например:
        <StyledUl>
          <StyledHidenLi
            onClick={() => handleChangeExample("детские аттракционы")}
          >
            детские аттракционы;
          </StyledHidenLi>
          <StyledLi onClick={() => handleChangeExample("пейнтбол")}>
            пейнтбол;
          </StyledLi>
          <StyledLi onClick={() => handleChangeExample("силомер")}>
            силомер;
          </StyledLi>
          <StyledLi onClick={() => handleChangeExample("батут")}>
            батут
          </StyledLi>
        </StyledUl>
      </ExampleText>
    </Wrapper>
  );
};

export default observer(Search);
