import React from "react";
import { observer } from "mobx-react-lite";
import useStore from "../../../hooks/use-store.hook";

import sortIcon from "../../../resources/icons/sort.svg";

import { Wrapper, Text, Icon } from "./sort-bar.styles";

const SortBar = () => {
  const store = useStore();

  return (
    <Wrapper>
      <Text>
        {store.filterStore.searchQuery ? "Найдено" : "Всего"} аттракционов:{" "}
        {store.productStore.count}
      </Text>
      <Text>
        <Icon src={sortIcon} />
        сортировать
      </Text>
    </Wrapper>
  );
};

export default observer(SortBar);
